


























































































































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Project from '@/modules/project/models/project';
import ProjectRepository from '@/modules/project/repository/project-repository';
import ProjectInfoItem from '@/modules/project/components/ProjectInfoItem.vue';
import {formatDate} from '@/utils/format';

@Component({
  components: {
    ProjectInfoItem,
  },
})
export default class ProjectDetailScreen extends Vue {
  @Prop() projectId!: string;
  project: Project | null = null;
  navigations: string[] = ['FUNDS RAISING', 'OVERVIEW', 'MEMBERS'];

  get fundInfos() {
    return [
      {
        icon: 'search',
        label: 'Token Price',
        text: `${this.project!.tokenInfo.price} USD`,
      }, {
        icon: 'search',
        label: 'Start Date',
        text: formatDate(this.project!.tokenInfo.startDate),
      }, {
        icon: 'search',
        label: 'End Date',
        text: formatDate(this.project!.tokenInfo.endDate),
      },
    ];
  }

  get projectInformation() {
    return [
      {
        icon: 'search',
        label: 'Country',
        text: this.project!.projectInfo.country,
      },
      {
        icon: 'search',
        label: 'Strategy/Type',
        text: 'Open-Ended Fund',
      },
      {
        icon: 'search',
        label: 'Industry',
        text: this.project!.projectInfo.industry,
      },
      {
        icon: 'search',
        label: 'Vintage',
        text: 2019,
      },
    ];
  }

  get dayLeft() {
    const dayLeft = new Date(this.project!.tokenInfo.endDate).getTime() - new Date().getTime();
    if (dayLeft < 0) {
      return 0;
    }

    return Math.floor(dayLeft / 1000 / 3600 / 24);
  }

  get raisedAmount(): number {
    if (!this.project) {
      return 0;
    }
    return (this.project.tokenInfo.tokenAmount - this.project.balance) * this.project.tokenInfo.price;
  }

  @Watch('projectId', {immediate: true})
  async onProjectIdChange(id: string) {
    this.project = await ProjectRepository.getProjectById(id);
  }

  goToInvestment() {
    this.$router.push({name: 'project-investment', params: {id: this.project!.id}});
  }
}
