import ExchangeScreen from '@/modules/exchange/views/ExchangeScreen.vue';
import ProcessExchangeScreen from '@/modules/exchange/views/ProcessExchangeScreen.vue';

const routers = [
  {
    path: '/exchange',
    name: 'exchange',
    component: ExchangeScreen,
  },
  {
    path: '/exchange/:transactionId',
    name: 'process-exchange',
    component: ProcessExchangeScreen,
    props: true,
  },
];

export default routers;
