import {db} from '@/setup/firebase';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import Project from '@/modules/project/models/project';
import * as firebase from 'firebase';

export default class ExchangeTransactionRepository {
  public static async createTransaction(transaction: ExchangeTransaction): Promise<boolean> {
    try {
      const {id, ...data} = transaction.getData() as any;
      const projectDocument = await db.collection('exchange-transactions').add(data);
      if (projectDocument) {
        return true;
      }
    } catch (e) {
      return false;
    }

    return false;
  }

  public static async getRunningTransactions(): Promise<ExchangeTransaction[]> {
    const transactions: ExchangeTransaction[] = [];

    const querySnapshot = await db.collection('exchange-transactions')
      .where('status', '==', 'running').get();

    querySnapshot.forEach((doc) => {
      const transaction = new ExchangeTransaction(doc.data(), doc.id);

      transactions.push(transaction);
    });

    return transactions;
  }

  public static async getMyHistoryTransactions(): Promise<ExchangeTransaction[]> {
    const transactions: ExchangeTransaction[] = [];

    const userId = firebase.auth().currentUser!.uid;

    let querySnapshot = await db.collection('exchange-transactions')
      .where('fromUserId', '==', userId)
      .where('status', '==', 'success')
      .get();

    querySnapshot.forEach((doc) => {
      const transaction = new ExchangeTransaction(doc.data(), doc.id);

      transactions.push(transaction);
    });

    querySnapshot = await db.collection('exchange-transactions')
      .where('toUserId', '==', userId)
      .where('status', '==', 'success')
      .get();

    querySnapshot.forEach((doc) => {
      const transaction = new ExchangeTransaction(doc.data(), doc.id);

      transactions.push(transaction);
    });

    return transactions;
  }

  public static async getTransactionById(id: string): Promise<ExchangeTransaction | null> {
    let transaction: ExchangeTransaction | null = null;

    const querySnapshot = await db.collection('exchange-transactions')
      .doc(id).get();

    transaction = new ExchangeTransaction(querySnapshot.data(), querySnapshot.id);

    return transaction;
  }

  public static async processSuccessTransaction(id: string, toUserId: string): Promise<boolean> {
    await db.collection('exchange-transactions')
      .doc(id).update({
        toUserId,
        status: 'success',
      });

    return true;
  }
}
