import store from '@/setup/store';

export const ifNotAuthenticated = (to: any, from: any, next: any) => {
  if (!store.getters.isAuth) {
    next();
    return;
  }
  next('/');
};

export const ifAuthenticated = (to: any, from: any, next: any) => {
  if (store.getters.isAuth) {
    next();
    return;
  }
  next('/login');
};
