































































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import Project from '@/modules/project/models/project';
import * as firebase from 'firebase';

@Component({})
export default class CreateProjectStep4 extends Vue {
  @Prop() loading!: boolean;
  @Prop() value!: Project;

  kycLevel = [
    {
      text: 'Level 0',
      value: 0,
      hint: '(require Email - Investors must verify the email before they can invest in this project)',
    },
    {
      text: 'Level 1',
      value: 1,
      hint: '(require KYC - Investors must verify the KYC before they can invest in this project)',
    },
  ];

  transactionRequired = [
    {
      value: true,
      text: 'Yes',
      hint: '(Investors must have permission from managers to buy and sell units)',
    },
    {
      value: false,
      text: 'No',
      hint: '(Investors can freely buy and sell units without the permission of managers)',
    },
  ];

  get owners() {
    return this.value.owners.map((owner) => {
      return {
        avatar: 'https://cdn.vuetifyjs.com/images/lists/1.jpg',
        email: firebase.auth().currentUser!.email,
        title: owner.position,
      };
    });
  }

  goBackAction() {
    this.$emit('back');
  }

  goToNextAction() {
    this.$emit('next');
  }

  addUser() {

  }
}
