




































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import ProjectFilter, {IProjectFilter} from '@/modules/project/components/ProjectFilter.vue';
import ExchangeTransaction from '@/modules/exchange/models/exchange-transaction';
import ExchangeTransactionRepository from '@/modules/exchange/repository/exchange-transaction-repository';

@Component({
  components: {
    ProjectFilter,
  },
})
export default class ExchangeScreen extends Vue {
  filter: IProjectFilter = {
    status: 'ALL',
    category: undefined,
    country: undefined,
    searchText: '',
  };
  headers = [
    {text: 'Time', align: 'center', sortable: false, value: 'time'},
    {text: 'Seller', align: 'center', sortable: false, value: 'seller'},
    {text: 'Asset', align: 'center', sortable: false, value: 'name'},
    {text: 'Quantity', value: 'amount', align: 'right', sortable: false},
    {text: 'Price', align: 'right', sortable: false},
    {text: 'Actions', align: 'center', sortable: false},
  ];
  loading = true;

  transactions: ExchangeTransaction[] = [];

  mounted() {
    this.fetchData();
  }

  submit(id: string) {
    this.$router.push({name: 'process-exchange', params: {transactionId: id}});
  }

  get userId() {
    return this.$store.state.user.user.id;
  }

  async fetchData() {
    this.loading = true;
    try {
      this.transactions = await ExchangeTransactionRepository.getRunningTransactions();
    } catch (e) {
    } finally {
      this.loading = false;
    }
  }
}
