







































































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import ALogo from '@/components/ALogo.vue';

@Component({
  components: {
    ALogo,
  },
})
export default class SignUpScreen extends Vue {
  private firstName: string = '';
  private lastName: string = '';
  private email: string = '';
  private password: string = '';
  private confirmPassword: string = '';
  private showPassword: boolean = false;
  private loading: boolean = false;
  private emailErrorMessage: string[] = [];

  @Watch('email')
  resetEmailErrorMessage() {
    this.emailErrorMessage = [];
  }

  private async signUpAction() {
    this.loading = true;
    try {
      const result = await this.$store.dispatch('signUserUp', {
        email: this.email,
        password: this.password,
        firstName: this.firstName,
        lastName: this.lastName,
      });

      if (result && result.message) {
        this.emailErrorMessage.push(result.message);
        return;
      }
      this.$router.push({name: 'Home'});
    } finally {
      this.loading = false;
    }
  }
}
