















































import {Component, Prop, Watch, Vue} from 'vue-property-decorator';
import ACategoryTypeSelect from '@/components/ACategoryTypeSelect.vue';
import ACountrySelect from '@/components/ACountrySelect.vue';

export interface IProjectFilter {
  status: string;
  category?: string;
  country?: string;
  searchText?: string;
}

@Component({
  components: {
    ACategoryTypeSelect,
    ACountrySelect,
  },
})
export default class ProjectFilter extends Vue {
  @Prop() value!: IProjectFilter;
  @Prop({default: false, type: Boolean}) noStatus!: string;
  statuses: string[] = ['ALL', 'UPCOMING', 'ONGOING', 'ENDED'];

  mounted() {
    this.value.status = 'ALL';
  }

  get status(): string {
    return this.value.status;
  }

  onSelectStatus(status: string) {
    this.value.status = status;
  }
}
