import BaseModel from '@/modules/base/models/base-model';

export default class ExchangeTransaction extends BaseModel {
  id: string = '';
  fromUserId: string = '';
  toUserId: string | null = null;
  sellerName: string = '';
  projectId: string = '';
  tokenAmount: number = 0;
  price: number = 0;
  total: number = 0;
  createdTime: number = 0;
  tokenName: string = '';
  status: string = '';

  constructor(data?: any, id?: string) {
    super();
    if (id) {
      this.id = id;
    }

    if (data) {
      this.fromUserId = data.fromUserId;
      this.toUserId = data.toUserId;
      this.projectId = data.projectId;
      this.sellerName = data.sellerName;
      this.tokenAmount = data.tokenAmount;
      this.price = data.price;
      this.createdTime = data.createdTime;
      this.tokenName = data.tokenName;
      this.status = data.status;
    }
  }
}
