import BaseModel from '@/modules/base/models/base-model';

export interface IOwner {
  id: string;
  position?: string;
}

export enum EProjectStatus {
  UPCOMING = 'UPCOMING',
  ONGOING = 'ONGOING',
  ENDED = 'ENDED',
}

export interface IProjectInfo {
  name: string;
  categoryType: string;
  country?: string;
  industry?: string;
  strategy?: string;
  about?: string;
  fundsHighlight?: string;
}

export default class Project extends BaseModel {
  balance: number = 0;
  createdAt: number = 0;
  documents: any;
  metaInfo: { website?: string; facebook?: string; twitter?: string; telegram?: string } = {};
  owners: IOwner[] = [];
  projectInfo: IProjectInfo
    = {
    name: '',
    categoryType: '',
    country: '',
  };
  setting: { kycLevelRequired: number; verifiedTransactionRequired: boolean } = {
    kycLevelRequired: 0, verifiedTransactionRequired: false,
  };
  tokenInfo: { startDate: number; endDate: number; tokenAmount: number; price: number; threshold: number }
    = {
    startDate: new Date(new Date().toDateString()).getTime(),
    endDate: new Date(new Date().toDateString()).getTime(),
    price: 0,
    threshold: 0,
    tokenAmount: 0,
  };

  id: string = '';

  constructor(data?: any, id?: string) {
    super();
    if (id) {
      this.id = id;
    }

    if (data) {
      this.balance = data.balance;
      this.documents = data.documents;
      this.metaInfo = data.metaInfo;
      this.owners = data.owners;
      this.projectInfo = data.projectInfo;
      this.setting = data.setting;
      this.tokenInfo = data.tokenInfo;
    }

  }

  get status(): EProjectStatus {
    const now = new Date().getTime();

    if (now < this.tokenInfo.startDate) {
      return EProjectStatus.UPCOMING;
    } else if (now >= this.tokenInfo.startDate && now <= this.tokenInfo.endDate) {
      return EProjectStatus.ONGOING;
    }
    return EProjectStatus.ENDED;
  }
}
